/** Container */
.anchor {
	text-decoration: none;
	color: black;
}

.cardContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	font-size: 15px;
}
/** Left */
.imageContainer {
	-ms-flex: 1 1;
	    flex: 1 1;
	-ms-flex-item-align: center;
	    align-self: center;
	text-align: center;
}

.image {
	border-radius: 50%;
	overflow: hidden;
}

/** Right */
.cardInfo {
	font-size: inherit;
	padding: 10px 0;
	-ms-flex: 3 1;
	    flex: 3 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}
/** Header */
.header {
	font-size: inherit;
	-ms-flex: 2 1;
	    flex: 2 1;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
}

.title {
	font-size: 20px;
	font-weight: bold;
	-ms-flex: 6 1;
	    flex: 6 1;
}

.description {
	font-size: 15px;
	font-weight: bold;
	font-style: italic;
	color: gray;
}

.date {
	font-size: inherit;
	-ms-flex: 1 1;
	    flex: 1 1;
	color: gray;
}

/** Details */
.details {
	font-size: inherit;
	-ms-flex: 6 1;
	    flex: 6 1;
	border-bottom: 1px solid gray;
}

.detail {
	margin: 0 0 5px 0 !important;
}

/** Extra */
.extra {
	font-size: inherit;
	-ms-flex: 2 1;
	    flex: 2 1;
	color: gray;
}



